/**
 * @desc SliderVerify
 *
 */

import React, { useMemo, useRef, useState } from "react";
import {
  StyleSheet,
  ViewStyle,
  View,
  Animated,
  PanResponder,
  Easing,
  Image,
  TouchableOpacity,
  ActivityIndicator,
  Text,
} from "react-native";
import { Ionicons } from "@expo/vector-icons";

type Props = {
  style?: ViewStyle;
  imageSize?: {
    puzzleWidth: number;
    puzzleHeight: number;
    puzzlePieceWidth: number;
  };
  puzzlePiecePosition?: {
    x: number;
    y: number;
  };
  // 校验loading
  verifying?: boolean;
  loading?: boolean;
  displayType?: string;
  slideTips?: string;
  // 刷新点击
  showRefresh?: boolean;
  onPressRefresh?: () => void;
  // 图片
  puzzle: string;
  puzzlePiece: string;
  // 拖拽停止事件
  onMoveRelease?: (x: number, failed: () => void, succsee: () => void) => void;
};

const generateSlidingStyles = (
  iconName: any,
  iconColor = "white",
  buttonColor: string,
  borderColor = buttonColor,
  indicatorColor: string
) => ({
  icon: <Ionicons name={iconName} size={25} color={iconColor} />,
  buttonColor,
  borderColor,
  indicatorColor,
});

const slidingStyles = {
  READY: generateSlidingStyles(
    "arrow-forward",
    "#5C6167",
    "white",
    "#e4e7eb",
    "#e4e7eb"
  ),
  MOVING: generateSlidingStyles(
    "arrow-forward",
    undefined,
    "#1991fa",
    undefined,
    "#d1e9fe"
  ),
  VERIFY_PASS: generateSlidingStyles(
    "checkmark",
    undefined,
    "#52ccba",
    undefined,
    "#d2f4ef"
  ),
  VERIFY_FAIL: generateSlidingStyles(
    "close",
    undefined,
    "#f57a7a",
    undefined,
    "#fce1e1"
  ),
};

export default function SliderVerify(props: Props) {
  const offsetXAnim = useRef(new Animated.Value(0)).current;
  const [slideStatus, setSlideStatus] = useState(slidingStyles.READY);
  const [moving, setMoving] = useState(false);
  const [result, setResult] = useState<boolean | null>(null);
  const [lastResult, setLastResult] = useState(false);

  const {
    imageSize = {
      puzzleWidth: 310,
      puzzleHeight: 155,
      puzzlePieceWidth: 55,
    },
    displayType = "",
    verifying = false,
    showRefresh = true,
    onPressRefresh = () => {},
    slideTips = "向右滑动左侧箭头填充拼图",
    puzzle,
    puzzlePiece,
    loading,
    puzzlePiecePosition = {
      x: 0,
      y: 0,
    },
    onMoveRelease,
  } = props || {};
  const { puzzleWidth, puzzlePieceWidth, puzzleHeight } = imageSize || {};
  // const maxMoving = puzzleWidth - puzzlePieceWidth;

  const panResponder = useMemo(
    () =>
      PanResponder.create({
        onStartShouldSetPanResponder: () => !result && !moving,
        onStartShouldSetPanResponderCapture: () => !result && !moving,
        onMoveShouldSetPanResponder: () => !result && !moving,
        onMoveShouldSetPanResponderCapture: () => !result && !moving,

        onPanResponderGrant: () => {
          setMoving(true);
          setResult(null);
          setSlideStatus(slidingStyles.MOVING);
        },
        onPanResponderMove: Animated.event([null, { dx: offsetXAnim }], {
          useNativeDriver: false,
          listener: () => {
            // console.log(event);
          },
        }),
        onPanResponderRelease: async (event, gestureState) => {
          const offset = gestureState.dx;
          onMoveRelease &&
            onMoveRelease(offset, handleVerifyFailed, handleVerifyPassed);
        },
      }),
    [lastResult, moving, puzzleWidth, puzzlePieceWidth, offsetXAnim]
  );

  const handleVerifyPassed = () => {
    setMoving(false);
    setResult(true);
    setSlideStatus(slidingStyles.VERIFY_PASS);
    setLastResult(true);
  };

  const handleVerifyFailed = () => {
    setResult(false);
    setSlideStatus(slidingStyles.VERIFY_FAIL);
    Animated.timing(offsetXAnim, {
      useNativeDriver: true,
      toValue: 0,
      delay: 200,
      easing: Easing.linear,
    }).start(() => {
      setSlideStatus(slidingStyles.READY);
      setMoving(false);
      setResult(null);
      setLastResult(false);
    });
  };

  const puzzleContainerStyle = StyleSheet.flatten([
    styles.puzzleContainer,
    {
      height: puzzleHeight,
      opacity:
        displayType === "triggered" ? (moving || !lastResult ? 1 : 0) : 1,
    },
    displayType === "triggered"
      ? { ...StyleSheet.absoluteFillObject, top: -(puzzleHeight + 20) }
      : styles.puzzleContainerEmbedded,
  ]);
  const puzzlePieceStyle = StyleSheet.flatten([
    {
      ...StyleSheet.absoluteFillObject,
      zIndex: 3,
      transform: [
        { translateX: offsetXAnim },
        // without this line this Animation will not render on Android while working fine on iOS
        { perspective: 1000 },
      ],
    },
  ]);
  const puzzlePieceImgStyle = {
    width: puzzlePieceWidth,
    height: puzzlePieceWidth,
    marginTop: puzzlePiecePosition.y,
  };
  const slideAnimatedStyle = StyleSheet.flatten([
    styles.slideIndicator,
    {
      borderColor: slideStatus.borderColor,
      backgroundColor: slideStatus.indicatorColor,
    },
  ]);
  const panResponderStyle = StyleSheet.flatten([
    styles.slider,
    {
      width: puzzlePieceWidth,
      backgroundColor: slideStatus.buttonColor,
      borderColor: slideStatus.borderColor,
      transform: [{ translateX: offsetXAnim }, { perspective: 1000 }],
    },
  ]);
  const containerStyle: { width: number; overflow: "hidden" } = {
    width: puzzleWidth,
    overflow: "hidden",
  };
  return (
    <View style={containerStyle}>
      <View style={puzzleContainerStyle}>
        {loading ? <ActivityIndicator color={"#888888"} /> : null}
        {!loading ? (
          <Image
            source={{ uri: puzzle }}
            style={styles.image}
            resizeMode="cover"
          />
        ) : (
          <Image
            source={require("../../../assets/images/common/logo_load.png")}
            style={styles.image}
            resizeMode="contain"
          />
        )}
        <Animated.View style={puzzlePieceStyle}>
          {!loading ? (
            <Image
              source={{ uri: puzzlePiece }}
              style={puzzlePieceImgStyle}
              resizeMode="cover"
            />
          ) : null}
        </Animated.View>
        {showRefresh && (
          <TouchableOpacity
            onPress={onPressRefresh}
            activeOpacity={0.7}
            style={styles.refresh}
            disabled={verifying}
          >
            <Ionicons name="ios-refresh" size={35} color="white" />
          </TouchableOpacity>
        )}
      </View>

      <View style={styles.slideBox}>
        <Animated.View style={slideAnimatedStyle} />

        <Animated.View
          {...panResponder.panHandlers}
          style={panResponderStyle}
          pointerEvents={loading ? "none" : "auto"}
        >
          {verifying ? (
            <ActivityIndicator color={"#888888"} />
          ) : (
            slideStatus.icon
          )}
        </Animated.View>
        {!moving && result === null && (
          <Text style={styles.slideTips}>{slideTips}</Text>
        )}
      </View>
    </View>
  );
}
const styles = StyleSheet.create({
  puzzleContainer: {
    width: "100%",
    // elevation: 10,
    backgroundColor: "ghostwhite",
    // shadowOffset: { width: 5, height: 5 },
    // shadowColor: 'grey',
    // shadowOpacity: 0.8,
    // shadowRadius: 5,
    alignItems: "center",
    justifyContent: "center",
  },
  puzzleContainerEmbedded: {
    marginBottom: 20,
  },
  image: {
    ...StyleSheet.absoluteFillObject,
    width: "100%",
    height: "100%",
    zIndex: 2,
  },
  refresh: {
    position: "absolute",
    right: 5,
    top: 1,
    zIndex: 3,
    backgroundColor: "transparent",
  },
  slideBox: {
    width: "100%",
    height: 45,
    borderWidth: 1,
    borderColor: "#e4e7eb",
    backgroundColor: "#f7f9fa",
  },
  slideIndicator: {
    height: "100%",
    borderWidth: 1,
    // borderRightWidth: 0
  },
  slider: {
    height: 43,
    position: "absolute",
    justifyContent: "center",
    alignItems: "center",
    borderWidth: 1,
  },
  slideTips: {
    position: "absolute",
    left: "30%",
    top: 13,
  },
});
