import DeviceInfo from "constants/DeviceInfo";
import React, { useEffect, useState } from "react";
import {
  Animated,
  StyleSheet,
  Text,
  TouchableOpacity,
  Easing,
  View,
} from "react-native";
import { AntDesign } from "@expo/vector-icons";
import { navigateTo } from "navigation/RootNavigation";

interface NoticeItem {
  id: string;
  content: string;
  [x: string]: string;
}

const componentHeight = 40;
const contentWidth = DeviceInfo.SCREEN_WIDTH - 40;
const spaceBetweenText = 40;

const HomeTopNotice = () => {
  const textList = [
    { id: "m1", content: "1、你有新的站内信，点击查看 >> " },
    {
      id: "m2",
      content:
        "2、这是一条系统消息，点击后进入消息详情，可以查看消息的具体内容，有时间限制",
    },
    {
      id: "m3",
      content: "3、新产品上线新产品上线新产品上线新产品上线，点我查看详情！",
    },
  ];
  const [textContentWidth, setTextContentWidth] = useState<number>(0);
  const [textViewWidth, setTextViewWidth] = useState<number>(0);
  const [transformX, setTransformX] = useState(
    new Animated.Value(contentWidth)
  );
  const [animation, setAnimation] = useState<Animated.CompositeAnimation>();

  useEffect(() => {
    if (textContentWidth > 0) {
      if (textContentWidth > contentWidth) {
        setAnimation(
          Animated.loop(
            Animated.timing(transformX, {
              toValue: -textContentWidth,
              duration: ((contentWidth + textContentWidth) / 60) * 1000,
              useNativeDriver: false,
              easing: Easing.linear,
            })
          )
        );
      } else {
        setTransformX(new Animated.Value(0));
      }
    }
  }, [textContentWidth]);
  useEffect(() => {
    animation && animation.start();
  }, [animation]);
  const textContent = () => {
    const cnt = textList.map((item, i) => {
      return (
        <TouchableOpacity
          key={"notice-message-" + i}
          activeOpacity={0.9}
          style={styles.noticeItem}
          onPress={() => {
            navigateTo("MessageDetailPage", { item });
          }}
        >
          <View style={styles.noticeItemView}>
            <Text style={styles.noticeItemText} numberOfLines={1}>
              {item.content}
            </Text>
          </View>
        </TouchableOpacity>
      );
    });
    return (
      <Animated.View
        style={{
          flexDirection: "row",
          width: textContentWidth,
          transform: [{ translateX: transformX }],
        }}
        onLayout={(e) => {
          const w = e.nativeEvent.layout.width;
          if (w != 0 && w != textViewWidth) {
            setTextViewWidth(w);
          }
        }}
      >
        {cnt}
      </Animated.View>
    );
  };
  const holderTextContent = () => {
    return (
      <View style={styles.holderContainer}>
        <Text
          style={styles.noticeItemText}
          numberOfLines={1}
          onLayout={(e) => {
            const w = e.nativeEvent.layout.width;
            if (w != 0 && w != textContentWidth) {
              setTextContentWidth(
                Math.max(
                  e.nativeEvent.layout.width +
                    spaceBetweenText * (textList.length - 1),
                  contentWidth
                )
              );
            }
          }}
        >
          {textList.map((item) => item.content).join("")}
        </Text>
      </View>
    );
  };
  return (
    <View style={styles.container}>
      <View style={styles.icon}>
        <AntDesign name="notification" size={24} color="black" />
      </View>
      <View style={styles.content}>
        {textContent()}
        {holderTextContent()}
      </View>
    </View>
  );
};
const styles = StyleSheet.create({
  container: {
    height: componentHeight,
    width: DeviceInfo.SCREEN_WIDTH,
    flexDirection: "row",
  },
  icon: {
    width: 40,
    height: 40,
    justifyContent: "center",
    alignItems: "center",
  },
  content: {
    flexDirection: "row",
    alignItems: "center",
    overflow: "hidden",
  },
  noticeItem: {
    height: componentHeight,
    justifyContent: "center",
  },
  noticeItemView: {
    flexDirection: "row",
    marginRight: spaceBetweenText,
  },
  noticeItemText: {
    fontSize: 14,
    fontWeight: "400",
  },
  holderContainer: {
    opacity: 0,
    position: "absolute",
    top: 20,
  },
});
export default HomeTopNotice;
