import React from "react";
import { createBottomTabNavigator } from "@react-navigation/bottom-tabs";
import { Ionicons } from "@expo/vector-icons";
import HomePage from "pages/root/HomePage";
import LoanPage from "pages/root/LoanPage";
import MinePage from "pages/root/MinePage";
import DeviceInfo from "constants/DeviceInfo";
import { navigateTo } from "navigation/RootNavigation";

const Tab = createBottomTabNavigator();

const BottomTab = () => {
  return (
    <Tab.Navigator
      screenOptions={({ route }) => ({
        tabBarIcon: ({ focused, color, size }) => {
          let iconName: string;
          if (route.name === "HomePage") {
            iconName = focused ? "color-filter" : "color-filter-outline";
          } else if (route.name === "LoanPage") {
            iconName = focused ? "briefcase" : "briefcase-outline";
          } else if (route.name === "MinePage") {
            iconName = focused ? "body" : "body-outline";
          }
          // @ts-ignore
          return <Ionicons name={iconName} size={size} color={color} />;
        },
        tabBarActiveTintColor: "tomato",
        tabBarInactiveTintColor: "gray",
      })}
    >
      <Tab.Screen
        name="HomePage"
        component={HomePage}
        options={{ headerShown: !DeviceInfo.isWeixin, title: "首页" }}
      />
      <Tab.Screen
        name="LoanPage"
        component={LoanPage}
        options={{ headerShown: !DeviceInfo.isWeixin, title: "贷款" }}
      />
      <Tab.Screen
        name="MinePage"
        component={MinePage}
        options={{ headerShown: !DeviceInfo.isWeixin, title: "我的" }}
        listeners={({ route }) => ({
          tabPress: (e) => {
            if (USER_TOKEN) {
              console.log("user online");
            } else {
              e.preventDefault();
              navigateTo("LoginPage");
            }
            console.log(">>> route ", route);
          },
        })}
      />
    </Tab.Navigator>
  );
};

export default BottomTab;
