/**
 * @desc TipCard
 *
 */

import React from 'react';
import {StyleSheet, ViewStyle, View} from 'react-native';
import {Ionicons} from '@expo/vector-icons';
import {Text} from '../base';

type Props = {
  style?: ViewStyle;
  title?: string;
  subTitle?: string;
  status?: 'primary' | 'warning' | 'danger' | 'other';
};

const generateSlidingStyles = (iconName: any, iconColor = 'white', backgroundColor: string, fontColor: string) => ({
  icon: <Ionicons name={iconName} size={24} color={iconColor} />,
  backgroundColor,
  fontColor,
});

const themeStyles: any = {
  primary: generateSlidingStyles('alert-circle', '#32509A', '#F0F5FF', '#32509A'),
  warning: generateSlidingStyles('alert-circle', '#FF9000', '#FFF9EC', '#FF9000'),
  danger: generateSlidingStyles('alert-circle', 'red', '#FFF9EC', '#FF9000'),
};

export default function TipCard(props: Props) {
  const {style, subTitle, title, status = 'primary'} = props || {};
  const {icon, backgroundColor, fontColor} = themeStyles[status];
  const containerStyle = StyleSheet.flatten([styles.container, {backgroundColor}, style]);
  const titleStyle = StyleSheet.flatten([styles.titleTip, {color: fontColor}]);
  const subTitleStyle = StyleSheet.flatten([styles.subTip, {color: fontColor}]);
  return (
    <View style={containerStyle}>
      {icon}
      <View style={styles.tipTitleView}>
        <Text style={titleStyle}>{title}</Text>
        {subTitle ? <Text style={subTitleStyle}>{subTitle}</Text> : null}
      </View>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flexDirection: 'row',
    padding: 16,
    backgroundColor: '#F0F5FF',
    borderRadius: 10,
  },
  subTip: {
    fontSize: 12,
    color: '#32509A',
    marginTop: 6,
    lineHeight: 15
  },
  titleTip: {
    fontSize: 14,
    lineHeight: 17,
    fontWeight: 'bold',
    color: '#32509A',
  },
  tipTitleView: {
    marginTop: 4, 
    marginLeft: 8,
    flex: 1,
  },
});
