/**
 * @desc TipItemContent
 *
 */

import React from 'react';
import {StyleSheet, ViewStyle, Text, TouchableOpacity} from 'react-native';
import {LinearGradient} from 'expo-linear-gradient';
import {Ionicons} from '@expo/vector-icons';

type Props = {
  style?: ViewStyle;
  tipText?: string;
  onClose?: () => void;
};

const colors = ['#4E7AFF', '#96BCFD'];

export default function TipItemContent(props: Props) {
  const {tipText, onClose} = props || {};
  return (
    <LinearGradient colors={colors} start={{x: 0, y: 0}} end={{x: 1, y: 1}} style={styles.tipContent}>
      <Text style={styles.tipText}>{tipText}</Text>
      <TouchableOpacity onPress={onClose}>
        <Ionicons name="md-close-outline" size={24} color="#fff" />
      </TouchableOpacity>
    </LinearGradient>
  );
}

const styles = StyleSheet.create({
  tipText: {
    color: '#fff',
  },
  tipContent: {
    paddingHorizontal: 16,
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    flex: 1,
    zIndex: 3,
    ...StyleSheet.absoluteFillObject,
  },
});
