import { StackActions } from "@react-navigation/native";
import { RouteName } from "./Routes";

let ref: any = null;
export const navigationRef = (el: any) => {
  if (ref === null) ref = el;
};

export const getNavigation = () => {
  return ref;
};

export const navigateTo = (name: RouteName, params?: NavigationParams) => {
  ref?.navigate(name, params);
};

export const navigateBack = () => {
  ref?.goBack();
};

export const navigateReplace = (name: RouteName, params?: NavigationParams) => {
  ref?.replace(name, params);
};

export const dangerouslyGetState = () => {
  return ref?.dangerouslyGetState();
};

export const navigatePush = (...args: any[]) => {
  // @ts-ignore
  ref?.dispatch(StackActions.push(...args));
};

export const navigateToTop = () => {
  ref?.dispatch(StackActions.popToTop());
};
