import * as React from "react";
import {
  Alert,
  Text,
  Image,
  StyleSheet,
  TouchableOpacity,
  View,
} from "react-native";
import * as ImagePicker from "expo-image-picker";
import ActionSheet from "components/custom/ActionSheet";

type ImageItem = {
  localUri: string;
};
const initialImages: Array<ImageItem> = [];

type ImagePickerSectionProps = {
  images: Array<string>;
  maxCount?: number;
  takeImageAction: () => void;
};

const ImagePickerSection = (props: ImagePickerSectionProps) => {
  const [selectedImages, setSelectedImage] = React.useState(initialImages);
  const [showActionSheet, setShowActionSheet] = React.useState(false);

  const getImageFromLocal = async () => {
    const permissionResult =
      await ImagePicker.requestMediaLibraryPermissionsAsync();
    if (permissionResult.granted === false) {
      // TODO:
      Alert.alert("Permission to access camera roll is required!");
      return;
    }
    const pickerResult = await ImagePicker.launchImageLibraryAsync();
    if (pickerResult.cancelled === true) return;
    setSelectedImage(selectedImages.concat({ localUri: pickerResult.uri }));
  };
  const getImageFromCamera = async () => {
    const permissionResult = await ImagePicker.requestCameraPermissionsAsync();
    if (permissionResult.granted === false) {
      // TODO:
      Alert.alert("Permission to access camera roll is required!");
      return;
    }
    const pickerResult = await ImagePicker.launchCameraAsync();
    if (pickerResult.cancelled === true) return;
    setSelectedImage(selectedImages.concat({ localUri: pickerResult.uri }));
  };
  const closeActionSheet = () => {
    setShowActionSheet(false);
  };
  const actionSheetCallback = (index: number) => {
    closeActionSheet();
    setTimeout(() => {
      if (index == 0) {
        getImageFromLocal();
      } else if (index == 1) {
        getImageFromCamera();
      }
    }, 500);
  };
  return (
    <View style={styles.container}>
      {selectedImages.map((item, index) => {
        return (
          <Image
            key={"selecteditem-" + index}
            source={{ uri: item.localUri }}
            style={styles.selectedImageItem}
          />
        );
      })}
      <TouchableOpacity
        onPress={() => setShowActionSheet(true)}
        style={styles.holdBtn}
      >
        <Text> + </Text>
      </TouchableOpacity>
      <ActionSheet
        show={showActionSheet}
        selectItems={["选择图片", "拍摄图片"]}
        actionCallback={actionSheetCallback}
      />
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flexDirection: "row",
  },
  selectedImageItem: {
    width: 100,
    height: 100,
    borderWidth: 1,
    borderColor: "blue",
    margin: 5,
  },
  holdBtn: {
    width: 100,
    height: 100,
    borderWidth: 1,
    borderColor: "blue",
    margin: 5,
  },
});

export default ImagePickerSection;
