/**
 * @desc Modal, Android iOS Web
 *
 */

import React from "react";
import {
  StyleSheet,
  ViewStyle,
  View,
  TouchableWithoutFeedback,
} from "react-native";
import { Portal as PortalView } from "react-native-portalize";

type Props = {
  style?: ViewStyle;
  visible: boolean;
  children: React.ReactNode;
  onCancel?: () => void;
};

export function Portal(props: Props) {
  const { style, visible, children, onCancel } = props;
  if (!visible) {
    return null;
  }
  const containerStyle = StyleSheet.flatten([styles.container, style]);
  return (
    <PortalView>
      <TouchableWithoutFeedback onPress={onCancel}>
        <View style={containerStyle}>{children}</View>
      </TouchableWithoutFeedback>
    </PortalView>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "rgba(0,0,0,0.6)",
  },
});
