import React, {useMemo, useState} from 'react';
import {
  StyleSheet,
  TextInputProps,
  TextInput as NativeTextInput,
  View,
  Text,
  TextStyle,
  ViewStyle,
  Platform,
  TouchableOpacity,
} from 'react-native';
import {Ionicons} from '@expo/vector-icons';
import TipItemContent from 'components/tip/TipItemContent';

export type InputProps = TextInputProps & {
  caption?: string;
  inputStyle?: ViewStyle;
  captionStyle?: TextStyle;
  iconName?: string;
  iconColor?: string;
  title?: string | undefined;
  subhead?: string | undefined;
  showBottomLine?: boolean;
  // basic: 浅灰底
  // danger: 粉底
  // disable: 深灰底
  // control: 白底
  status?: 'basic' | 'danger' | 'disable' | 'control';
  tipText?: string;
};

export const themeStyles = {
  basic: '#F6F7F9',
  danger: '#FF00000D',
  disable: '#E9EAEE',
  control: '#FFFFFF',
};

export function TextInput(props: InputProps) {
  const {
    caption,
    style,
    inputStyle,
    iconName,
    title,
    showBottomLine,
    status = 'basic',
    editable = true,
    tipText,
    subhead,
    iconColor = '#4E7AFF',
    placeholderTextColor = '#E0E0E0',
    ...inputProps
  } = props;
  const iconView = useMemo(() => {
    // @ts-ignore
    return iconName ? <Ionicons style={styles.icon} name={iconName} size={24} color={iconColor} /> : null;
  }, [iconName]);
  const [visible, setVisible] = useState(false);

  const containerStyle = useMemo(() => {
    const bottomLineStyle = showBottomLine ? styles.bottomLine : {};
    let backgroundColor = caption ? themeStyles.danger : themeStyles[status];
    backgroundColor = editable ? backgroundColor : themeStyles.disable;
    return StyleSheet.flatten([styles.container, bottomLineStyle, {backgroundColor}, style]);
  }, [caption, showBottomLine, status, style, editable]);

  const titleView = useMemo(() => {
    const color = editable ? '#000000A6' : '#00000059';
    const titleStyle = StyleSheet.flatten([styles.title, {color}]);
    return title ? (
      <Text style={titleStyle}>
        {title}
        <Text style={styles.subhead}> {subhead}</Text>
      </Text>
    ) : null;
  }, [title, editable]);

  const inputViewStyle = useMemo(() => {
    const color = editable ? '#000000D9' : '#00000073';
    return StyleSheet.flatten([styles.input, {color}, inputStyle]);
  }, [editable]);
  // const inputCaptionStyle = StyleSheet.flatten([styles.caption, captionStyle]);

  const onHelp = () => {
    setVisible(true);
  };
  const onHiddenTip = () => {
    setVisible(false);
  };

  const tipContainer = () => {
    if (!tipText || !visible) {
      return null;
    }
    return <TipItemContent tipText={tipText} onClose={onHiddenTip} />;
  };

  return (
    <View style={containerStyle}>
      <View style={styles.titleView}>
        {iconView}
        {titleView}
        {tipText ? (
          <TouchableOpacity onPress={onHelp}>
            <Ionicons style={styles.icon} name={'help-circle-outline'} size={16} color="#4E7AFF" />
          </TouchableOpacity>
        ) : null}
      </View>
      <NativeTextInput
        style={inputViewStyle}
        textAlign={'right'}
        editable={editable}
        placeholderTextColor={placeholderTextColor}
        {...inputProps}
      />
      {tipContainer()}
      {/*{caption ? <Text style={inputCaptionStyle}>{caption}</Text> : null}*/}
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    height: 40,
    // backgroundColor: '#F6F7F9',
    borderRadius: 10,
    overflow: 'hidden',
    paddingHorizontal: 16,
    alignItems: 'center',
    flexDirection: 'row',
    justifyContent: 'space-between',
  },
  input: Platform.select({
    default: {
      flex: 1,
      height: 40,
    },
    web: {
      flex: 1,
      outlineStyle: 'none',
      outlineWidth: 0,
      height: 40,
    },
  }),
  caption: {
    marginTop: 2,
    fontSize: 10,
    color: 'red',
  },
  icon: {
    marginRight: 6,
  },
  title: {
    color: '#000000A6',
    fontSize: 14,
  },
  titleView: {
    flexDirection: 'row',
  },
  bottomLine: {
    borderBottomColor: '#E9E9E9',
    borderBottomWidth: 1,
  },
  subhead: {
    color: '#00000073',
    fontSize: 12,
    marginLeft: 8,
  },
});
