import { setItem } from "./storageUtil";

interface User {
  token: string;
  userId?: string;
}

export const loginWithData = (userInfo: User) => {
  USER_TOKEN = userInfo?.token;
  setItem("USER_TOKEN", USER_TOKEN);
};

export const logout = () => {
  USER_TOKEN = "";
  setItem("USER_TOKEN", USER_TOKEN);
};
