import React from "react";
import { StyleSheet, ViewStyle, View } from "react-native";
import { Portal } from "components/base";
import DeviceInfo from "constants/DeviceInfo";
// import LottieView from 'lottie-react-native';

const loadingSize = 80;
const top = (DeviceInfo.SCREEN_HEIGHT - loadingSize) / 2;

type Props = {
  style?: ViewStyle;
  visible: boolean;
};

export default function Loading(props: Props) {
  const { visible, style } = props || {};
  const modalStyle = StyleSheet.flatten([styles.container, style]);

  return (
    <Portal visible={visible} style={modalStyle}>
      <View style={styles.loading_view}>
        {/* <LottieView source={require('../../assets/file/loading.json')} style={styles.pic} autoPlay={visible} loop /> */}
      </View>
    </Portal>
  );
}

const styles = StyleSheet.create({
  container: {
    width: DeviceInfo.SCREEN_WIDTH,
    height: DeviceInfo.SCREEN_HEIGHT,
    backgroundColor: "rgba(0,0,0,0.5)", //'transparent',
  },
  loading_view: {
    backgroundColor: "rgba(255,255,255,1)",
    justifyContent: "center",
    alignItems: "center",
    position: "absolute",
    top: top,
    alignSelf: "center",
    height: loadingSize,
    width: loadingSize,
    borderRadius: 10,
    overflow: "hidden",
    borderWidth: 1.5,
    borderColor: "#F0F0F0",
  },
  pic: {
    height: loadingSize * 0.8,
    width: loadingSize * 0.8,
  },
});
