import React, { useMemo } from "react";
import { StyleSheet, ViewStyle, TouchableOpacity } from "react-native";
import { Text, View } from "../base";
import { Ionicons } from "@expo/vector-icons";
import * as navigation from "../../navigation/RootNavigation";

type Props = {
  style?: ViewStyle;
  title?: string;
  onChange: any;
  checked: boolean;
  protocols?: { fileName: string; fileUrl: string }[];
};

export function CheckProtocols(props: Props) {
  const { style, title, protocols, onChange, checked } = props;
  const containerStyle = StyleSheet.flatten([style, styles.protocolView]);
  const protocolsLength = protocols?.length;

  const iconName = useMemo(() => {
    return checked ? "checkbox" : "square-outline";
  }, [checked]);

  const onPressProtocol = (protocol: { fileUrl: string; fileName: string }) => {
    const url = protocol?.fileUrl || "";
    const name = protocol?.fileName || "";

    if (url) {
      const fileArr = url.split(".") || [];
      const isValid = Array.isArray(fileArr) && fileArr.length >= 2;
      const length = isValid ? fileArr.length : 0;
      const fileSuffix = isValid ? fileArr[length - 1] : "";
      // navigation.navigate(fileSuffix == 'pdf'? "PdfPreviewPage" : 'Xieyi', {uri: url, fileName: name});
      // navigation.navigate("PdfPreviewPage", {uri: url, fileName: name});
    } else {
      toast.show("协议链接无效");
    }
  };

  const onPressCheckBox = () => {
    onChange && onChange(!checked);
  };

  const renderCheckBox = () => {
    return (
      <TouchableOpacity
        style={styles.checkBox}
        hitSlop={{ top: 10, right: 15, bottom: 10, left: 15 }}
        onPress={onPressCheckBox}
      >
        <Ionicons name={iconName} size={20} color="#4E7AFF" />
      </TouchableOpacity>
    );
  };

  return (
    <View style={containerStyle}>
      {renderCheckBox()}
      <View style={styles.protocols}>
        <Text>
          {title ? (
            <Text style={styles.title} onPress={onPressCheckBox}>
              {title}
            </Text>
          ) : null}

          {Array.isArray(protocols) &&
            protocols.map((protocol, index) => {
              const { fileName } = protocol || {};
              const isLast = protocolsLength && protocolsLength <= index + 1;
              return (
                <Text
                  style={styles.label}
                  key={index}
                  onPress={() => onPressProtocol(protocol)}
                >
                  《{fileName}》{isLast ? "" : "、"}
                </Text>
              );
            })}
        </Text>
      </View>
    </View>
  );
}

const styles = StyleSheet.create({
  protocolView: {
    flexDirection: "row",
    backgroundColor: "transparent",
    minHeight: 24,
  },
  protocolRadio: {
    marginRight: 8,
    width: 18,
    height: 18,
  },
  protocols: {
    flexDirection: "row",
    alignItems: "center",
    flexWrap: "wrap",
    flex: 1,
    marginTop: -1,
  },
  title: {
    marginLeft: 2,
    fontSize: 14,
    lineHeight: 22,
  },
  tabBarIcon: {
    marginBottom: -3,
  },
  label: {
    color: "#4E7AFF",
    fontSize: 14,
    lineHeight: 22,
  },
  closeIcon: {
    position: "absolute",
    right: 6,
    top: 36,
    zIndex: 1,
  },
  pdfView: { flex: 1, margin: 16, borderRadius: 10, overflow: "hidden" },
  pdf: {
    flex: 1,
  },
  checkBox: {
    marginRight: 6,
  },
});
