import { Button } from "components/base";
import { FormInput } from "components/form/FormInput";
import { FormInputVerify } from "components/form/FormInputVerify";
import { API_SENG_MESSAGE_CODE } from "constants/apiPath";
import React from "react";
import { useForm } from "react-hook-form";
import { ScrollView, StyleSheet, View } from "react-native";
import { FormInputVerifyType } from "types";
import { regexAuthCode_1, regxPhoneNumber } from "utils/validate/regexs";

const ResetPassPage = () => {
  const {
    control,
    getValues,
    formState: { errors, isValid, isDirty },
  } = useForm();
  const { phoneNum, verifyCode, password } = getValues();
  return (
    <View style={styles.container}>
      <ScrollView style={styles.content}>
        <FormInput
          name={"phoneNum"}
          control={control}
          placeholder={"请输入手机号"}
          placeholderTextColor={"#BFBFC3"}
          textAlign={"left"}
          keyboardType={"number-pad"}
          style={styles.formInput}
          maxLength={11}
          rules={{
            required: true,
            pattern: {
              value: regxPhoneNumber,
              message: "请输入正确的手机号",
            },
          }}
          caption={errors?.phoneNum?.message}
          iconName={"md-phone-portrait-outline"}
        />
        <FormInput
          name="password"
          control={control}
          placeholder={"请输入或设置8-16位登录密码"}
          placeholderTextColor={"#BFBFC3"}
          textAlign={"left"}
          keyboardType={"default"}
          style={styles.formInput}
          secureTextEntry={true}
          caption={errors?.password?.message}
          iconName={"lock-closed-outline"}
        />

        <FormInputVerify
          name={"verifyCode"}
          verifyType={FormInputVerifyType.ResetPass}
          style={styles.formInput}
          textAlign={"left"}
          showSlider
          iconName={"shield-outline"}
          buttonTitle={"发送验证码"}
          placeholder={"请输入短信验证码"}
          keyboardType={"number-pad"}
          requestConfig={{
            url: API_SENG_MESSAGE_CODE,
            method: "POST",
            data: {
              mobile: phoneNum,
              smsType: 2,
            },
          }}
          maxLength={6}
          disabledButton={!phoneNum}
          control={control}
          caption={errors?.verifyCode?.message || ""}
          rules={{
            pattern: {
              value: regexAuthCode_1,
              message: "请输入正确格式的验证码",
            },
          }}
        />
        <Button
          style={styles.confirmButton}
          title={"确定"}
          disabled={!isValid || !isDirty}
          // loading={isloading}
          // onPress={onResetPress}
        />
      </ScrollView>
    </View>
  );
};
const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#ffffff",
  },
  content: {
    padding: 15,
  },
  formInput: {
    marginBottom: 10,
  },
  confirmButton: {
    marginTop: 50,
  },
});
export default ResetPassPage;
