/**
 * 姓名相关
 */

// 姓名校验: 中文、空格
export const regexName =
  /^[a-zA-Z]+([a-zA-Z\s]+[a-zA-Z]+)*$|^[\u4e00-\u9fa5]+$/;
// 用户名校验
export const regxUserName = /^[0-9a-zA-Z]+$/;
// 企业名称校验
export const regexEnterpriseName = /^[\u4e00-\u9fa5()（）a-zA-Z]{4,200}$/;

/**
 * 手机号（只校验位数 #SCFP-4837）
 */
export const regxPhoneNumber = /^1\d{10}$/;
// export const regxPassNumber = /(?!^(\d+|[a-zA-Z]+|[~!@#$%^&*()_+`\-={}:";'<>?,.\/\\]+)$)^[\w~!@#$%^&*()_+`\-={}:";'<>?,.\/\\]{8,20}$/;
export const phoneNumber = /^(\d{11}|\d{15})$/;

//工号 (正整数 0-9)
export const workNumber = /^[0-9]\d*$/;
//年收入 正整整 不以0开头
export const regexPositiveNum = /^[1-9]\d*$/;
//   ^((0{1}\.\d{1,2})|([1-9]\d*\.{1}\d{1,2})|([1-9]+\d*))$
/**
 * 银行卡号
 */
export const bankCard =
  /^([1-9]{1})(\d{11}|\d{15}|\d{16}|\d{17}|\d{18}|\d{19}|\d{20})$/; //建行16、19，农行19，工行19、交通17、民生16、兴业18、招行12、16、19
/**
 * 验证码
 */
export const regexAuthCode = /^.{6}$/;
export const regexAuthCode_1 = /^\d{4}$/;

/**
 * 金额
 */
export const regexDecimal = /^\d+(\.\d+)?$/;
export const regexDecimalLimit2 = /^(?!0+$)(?!0*\.0*$)\d{1,8}(\.\d{1,2})?$/;

export const priceValite = (val: string, isGuaCar = false): boolean => {
  if (isGuaCar) {
    if (/^\d+\.$/.test(val)) {
      return false;
    }
    if (Number(val) == 0) {
      return true;
    }
  }

  if (Number(val) == 0) {
    return false;
  }

  const regx = /^[1-9]\d{0,7}$/;
  if (!/^[0-9]{1,8}([.]*\d{1,2})*$/.test(val)) {
    return false;
  } else {
    if (val.indexOf(".") != -1) {
      const intNum: string[] = val.split(".");
      if (intNum[0].length > 1) {
        return !!(
          intNum[0].match(regx) &&
          intNum[1].length <= 2 &&
          intNum[0].length <= 8
        );
      } else {
        return intNum[0] == "0" && intNum[1].length <= 2;
      }
    } else {
      return !!val.match(regx);
    }
  }
};

//利率 ：100以内（不含100） 最多两位小数，若无小数，整数部分最大99
export const regexMoneyRate =
  /^(99|(([1-9]){1}[0-9]?|0{1})((\.)([0-9]){1,2})?)$/;

export const dkjeImpl = (val: string, isGuaCar = false): boolean => {
  const regx = /^[1-9]\d*$/;
  if (!isGuaCar) {
    return regx.test(val) && val != "0";
  } else {
    return /(^[1-9]\d*$)|(^[0]$)/.test(val);
  }
};
