import { Dimensions, Platform, StatusBar } from "react-native";

const SCREEN_WIDTH = Dimensions.get("window").width;
const SCREEN_HEIGHT = Dimensions.get("window").height;
const platform = Platform.OS; // PlatformOSType = 'ios' | 'android' | 'macos' | 'windows' | 'web' | 'native';
const isAndroid = platform === "android";
const isIOS = platform === "ios";
const isWeb = platform === "web";
// @ts-ignore
const ua = window?.navigator.userAgent.toLowerCase();
const isWeixin = ua?.match(/MicroMessenger/i) == "micromessenger";
const iPhoneX_Min_Width = 375;
const iPhoneX_Min_Height = 812;
const isIPhoneXSeries =
  isIOS &&
  SCREEN_WIDTH >= iPhoneX_Min_Width &&
  SCREEN_HEIGHT >= iPhoneX_Min_Height;
const dangerBottomHeight = isIPhoneXSeries ? 34 : 0;
const statusBarHeight = isIOS
  ? isIPhoneXSeries
    ? 44
    : 20
  : StatusBar.currentHeight;

export default {
  SCREEN_HEIGHT,
  SCREEN_WIDTH,
  isAndroid,
  isIOS,
  isWeb,
  isWeixin,
  statusBarHeight,
  dangerBottomHeight,
  isIPhoneXSeries,
};
