import * as React from "react";
import { useEffect } from "react";
import { ScrollView, StyleSheet, View } from "react-native";
import { Button, Text } from "components/base";
import ImagePickerSingle from "components/imagePicker/ImagePickerSingle";
import TipCard from "components/tip/TipCard";
import DeviceInfo from "constants/DeviceInfo";
import { API_ID_CARD_OCR, API_SAVE_ID_CARD_INFO } from "constants/apiPath";
import useAxios from "axios-hooks";
import { navigateBack } from "navigation/RootNavigation";

const IDCardAuthPage = () => {
  const [idCardFrontPath, setIdCardFrontPath] = React.useState("");
  const [idCardBackPath, setIdCardBackPath] = React.useState("");
  const [idCardInfoSaveSuccess, setIdCardInfoSaveSuccess] =
    React.useState(false);
  const [{ loading: ocrLoading, data: ocrData, error }, idCardOCRRequest] =
    useAxios(
      {
        url: API_ID_CARD_OCR + idCardFrontPath,
        method: "GET",
      },
      { manual: true }
    );
  const [{ loading: saveLoading }, saveIdCardOCR] = useAxios(
    {
      url: API_SAVE_ID_CARD_INFO,
      method: "POST",
      data: {
        ...ocrData?.frontOcr,
        frontFileId: idCardFrontPath,
        backFileId: idCardBackPath,
      },
    },
    { manual: true }
  );
  console.log(" ocrdata => ", ocrData);
  const submitAction = () => {
    if (idCardInfoSaveSuccess) {
      navigateBack();
    } else {
      if (ocrData?.frontOcr) {
        saveIdCardOCR().then((e) => {
          setIdCardInfoSaveSuccess(true);
        });
      }
    }
  };
  useEffect(() => {
    if (idCardFrontPath) {
      idCardOCRRequest();
    }
  }, [idCardFrontPath]);
  return (
    <View style={styles.container}>
      {idCardInfoSaveSuccess ? (
        <View
          style={{ flex: 1, justifyContent: "center", alignItems: "center" }}
        >
          <Text>保存成功</Text>
        </View>
      ) : (
        <ScrollView contentContainerStyle={styles.contentContainer}>
          <TipCard
            style={styles.tipCard}
            title={"为了您的资金安全，请进行身份核实"}
          />
          <ImagePickerSingle
            borderRadius={10}
            style={styles.imagePicker}
            onChange={(data) => {
              setIdCardFrontPath(data?.localUri);
            }}
          >
            <View style={styles.pickerContent}>
              <View style={{ flex: 1 }}></View>
              <Text style={styles.pickerText}>
                {"点击上传身份证人面像照片"}
              </Text>
            </View>
          </ImagePickerSingle>
          <ImagePickerSingle
            borderRadius={10}
            style={styles.imagePicker}
            onChange={(data) => setIdCardBackPath(data?.localUri)}
          >
            <View style={styles.pickerContent}>
              <View style={{ flex: 1 }}></View>
              <Text style={styles.pickerText}>
                {"点击上传身份证国徽面照片"}
              </Text>
            </View>
          </ImagePickerSingle>
          <View style={styles.ocrDataView}>
            <Text>{JSON.stringify(ocrData?.frontOcr)}</Text>
          </View>
        </ScrollView>
      )}
      <Button
        title={idCardInfoSaveSuccess ? "确认" : "下一步"}
        onPress={submitAction}
        disabled={!(idCardFrontPath && idCardBackPath)}
        style={styles.confirmButton}
      />
    </View>
  );
};
const styles = StyleSheet.create({
  container: {
    width: DeviceInfo.SCREEN_WIDTH,
    height: DeviceInfo.SCREEN_HEIGHT - (DeviceInfo.isWeixin ? 0 : 64),
  },
  contentContainer: {
    flex: 1,
  },
  tipCard: {
    margin: 16,
  },
  imagePicker: {
    width: DeviceInfo.SCREEN_WIDTH - 20 * 2,
    height: ((DeviceInfo.SCREEN_WIDTH - 20 * 2) * 3) / 5,
    marginBottom: 16,
    alignSelf: "center",
  },
  pickerContent: {
    overflow: "hidden",
    backgroundColor: "#eee",
    flex: 1,
  },
  pickerText: {
    textAlign: "center",
    color: "#999",
    lineHeight: 20,
    marginBottom: 20,
  },
  ocrDataView: {
    padding: 20,
    backgroundColor: "orange",
  },
  confirmButton: {
    margin: 16,
  },
});
export default IDCardAuthPage;
