import React, { useEffect, useMemo, useState } from "react";
import {
  StyleSheet,
  View,
  ViewStyle,
  TouchableOpacity,
  Text,
} from "react-native";
import { Button, InputProps, Portal, TextInput } from "../base";
import useAxios from "axios-hooks";
import { AxiosRequestConfig } from "axios";
import { useCountdown } from "hooks/useCountdown";
import { API_SLIDE_IMAGE, API_SLIDE_IMAGE_RESULT } from "constants/apiPath";
import SliderVerify from "components/verify/SliderVerify";
import to from "await-to-js";
import { useToast } from "react-native-toast-notifications";

export type InputVerifyProps = {
  requestConfig: AxiosRequestConfig;
  buttonTitle?: string;
  disabledButton?: boolean;
  inputStyle?: ViewStyle;
  sendCallback?: () => void;
  showSlider?: boolean; // 是否显示滑动验证码
  name: string;
  verifyType: string;
} & InputProps;

const wrapImageData = (base64Str: string) =>
  `data:image/png;base64,${base64Str}`;

export function InputVerify(props: InputVerifyProps) {
  const {
    requestConfig,
    style,
    buttonTitle = "",
    disabledButton,
    inputStyle,
    showSlider,
    sendCallback,
    verifyType,
    ...textInputProps
  } = props || {};
  const toast = useToast();

  const { url, method, params, data, ...config } = requestConfig || {};
  const [{ loading }, executeFetch] = useAxios({}, { manual: true });
  const [curSecond, startTime] = useCountdown(undefined, verifyType);
  const [btnTitle, disabled] = useMemo(() => {
    const btnTitle = curSecond ? String(curSecond) + " s" : buttonTitle;
    const disabled = curSecond > 0 || disabledButton;
    return [btnTitle, disabled];
  }, [curSecond, buttonTitle, disabledButton]);
  const [visible, setVisible] = useState(false);

  // 获取
  const [{ loading: imgLoading, data: imgData }, refreshSlideImage] = useAxios(
    { url: API_SLIDE_IMAGE },
    { manual: true }
  );

  const { sessionId, cutoutImage, shadeImage, x, y } = imgData?.data || {};

  // 校验
  const [{ loading: verifyLoading }, verifyFetch] = useAxios(
    {},
    { manual: true }
  );

  useEffect(() => {
    // 请求拼图验证码
    showSlider && refreshSlideImage();
  }, []);

  const onPress = () => {
    if (showSlider) {
      setVisible(true);
      return;
    }
    onSendMsgCode();
  };
  const onSendMsgCode = async (messageToken?: string) => {
    const paramsObj = ["get", "GET", undefined].includes(method)
      ? { params: { ...params } }
      : { data: { ...data } };
    // if (showSlider) {
    //   // @ts-ignore
    //   paramsObj.messageToken = messageToken;
    // }
    const [err, messageRes] = await to(
      executeFetch({ url: url, method, ...config, ...paramsObj })
    );
    if (err) {
      console.log("eerr ", err);
      toast.show("验证码获取失败");
    }
    if (messageRes) {
      console.log("eerr ", err);
      toast.show("验证码获取成功");
      startTime();
      sendCallback && sendCallback();
    }
  };

  const onRefreshSlideImage = () => {
    refreshSlideImage();
  };
  const onMoveRelease = (
    offset: number,
    handleVerifyFailed: () => void,
    handleVerifyPassed: () => void
  ) => {
    verifyFetch({
      url: API_SLIDE_IMAGE_RESULT,
      params: {
        sessionId,
        x: offset,
        y,
      },
    })
      .then((data) => {
        console.log(">>> verify data ", data);

        const { result, messageToken } = data?.data?.data || {};
        if (result) {
          handleVerifyPassed();
          setTimeout(() => {
            setVisible(false);
            onSendMsgCode(messageToken);
          }, 800);
        } else {
          refreshSlideImage();
          handleVerifyFailed();
        }
      })
      .catch(() => {
        handleVerifyFailed();
      });
  };
  const closeSlider = () => {
    setVisible(false);
  };

  const renderModalView = () => {
    const puzzle = wrapImageData(shadeImage);
    const puzzlePiece = wrapImageData(cutoutImage);
    return (
      <View style={styles.modalView}>
        <View style={styles.content}>
          <SliderVerify
            onPressRefresh={onRefreshSlideImage}
            loading={imgLoading}
            puzzle={puzzle}
            puzzlePiece={puzzlePiece}
            puzzlePiecePosition={{ x, y }}
            onMoveRelease={onMoveRelease}
            verifying={verifyLoading || imgLoading}
          />
        </View>
        <TouchableOpacity onPress={closeSlider} style={styles.closeButton}>
          <Text style={{ color: "#fff" }}>X</Text>
        </TouchableOpacity>
      </View>
    );
  };

  const containerStyle = StyleSheet.flatten([styles.container, style]);
  const inputViewStyle = StyleSheet.flatten([styles.input, inputStyle]);
  return (
    <View style={containerStyle}>
      <TextInput
        textAlign={"left"}
        style={inputViewStyle}
        {...textInputProps}
      />
      <Button
        style={styles.btn}
        disabled={disabled}
        title={btnTitle}
        loading={loading}
        onPress={onPress}
      />
      <Portal visible={visible} style={styles.modalSty}>
        {renderModalView()}
      </Portal>
    </View>
  );
}

const styles = StyleSheet.create({
  modalSty: { flex: 1 },
  container: {
    flexDirection: "row",
    height: 44,
    backgroundColor: "#fff",
    alignItems: "center",
    overflow: "hidden",
  },
  input: {
    flex: 1,
    marginRight: 10,
  },
  btn: {
    minWidth: 120,
  },
  modalView: {
    flex: 1,
    alignItems: "center",
    justifyContent: "center",
    backgroundColor: "rgba(0,0,0,.3)",
    overflow: "hidden",
  },
  content: {
    padding: 20,
    backgroundColor: "#fff",
    borderRadius: 16,
    overflow: "hidden",
  },
  closeButton: {
    width: 30,
    height: 30,
    borderRadius: 15,
    backgroundColor: "transparent",
    borderWidth: 1,
    borderColor: "#fff",
    marginTop: 50,
    justifyContent: "center",
    alignItems: "center",
  },
});
