import { Button, Portal } from "components/base";
import TipCard from "components/tip/TipCard";
import DeviceInfo from "constants/DeviceInfo";
import React, { useEffect, useState } from "react";
import { StyleSheet, View, Animated, Pressable } from "react-native";

const initialWidth = (DeviceInfo.SCREEN_WIDTH - 16 * 2 - 16) / 2;
const maxWidth = 200;
const minWidth = DeviceInfo.SCREEN_WIDTH - 16 * 2 - 16 - maxWidth;
const maxFontSize = 16;
const minFontSize = 14;
const ChooseTypePage = () => {
  const [item_0_width] = useState<Animated.Value>(
    new Animated.Value(initialWidth)
  );
  const [item_1_width] = useState<Animated.Value>(
    new Animated.Value(initialWidth)
  );
  const [item_0_font_size] = useState<Animated.Value>(new Animated.Value(16));
  const [item_1_font_size] = useState<Animated.Value>(new Animated.Value(16));
  const [selectedIndex, setSelectedIndex] = useState<number>();
  const [tipModalVisible, setTipModalVisible] = useState<boolean>(false);

  const selectedWithIndex = (index: number) => {
    setSelectedIndex(index);
  };
  useEffect(() => {
    if (selectedIndex != undefined) {
      Animated.timing(item_0_width, {
        toValue: selectedIndex == 0 ? maxWidth : minWidth,
        duration: 300,
        useNativeDriver: false,
      }).start();
      Animated.timing(item_1_width, {
        toValue: selectedIndex == 1 ? maxWidth : minWidth,
        duration: 300,
        useNativeDriver: false,
      }).start();
      Animated.timing(item_0_font_size, {
        toValue: selectedIndex == 0 ? maxFontSize : minFontSize,
        duration: 300,
        useNativeDriver: false,
      }).start();
      Animated.timing(item_1_font_size, {
        toValue: selectedIndex == 1 ? maxFontSize : minFontSize,
        duration: 300,
        useNativeDriver: false,
      }).start();
    }
  }, [selectedIndex]);
  const confirmAction = () => {
    setTipModalVisible(true);
  };
  const closeModal = () => {
    setTipModalVisible(false);
  };
  const confirm = () => {
    closeModal();
  };
  return (
    <View style={styles.container}>
      <TipCard
        style={styles.tipCard}
        title={"请选择您的身份"}
        subTitle={"身份一旦确认不允许修改！"}
      />
      <View style={styles.selectContainer}>
        <Pressable onPress={() => selectedWithIndex(0)}>
          <Animated.Image
            source={
              selectedIndex == 0
                ? require("../../../assets/images/auth/individual_color.png")
                : require("../../../assets/images/auth/individual.png")
            }
            resizeMode="cover"
            style={[styles.itemImage, { width: item_0_width }]}
          />
          <View style={styles.itemTextView}>
            <Animated.Text
              style={[styles.itemText, { fontSize: item_0_font_size }]}
            >
              我要买车
            </Animated.Text>
          </View>
        </Pressable>
        <View style={styles.separateView} />
        <Pressable onPress={() => selectedWithIndex(1)}>
          <Animated.Image
            source={
              selectedIndex == 1
                ? require("../../../assets/images/auth/company_color.png")
                : require("../../../assets/images/auth/company.png")
            }
            resizeMode="cover"
            style={[styles.itemImage, { width: item_1_width }]}
          />
          <View style={styles.itemTextView}>
            <Animated.Text
              style={[styles.itemText, { fontSize: item_1_font_size }]}
            >
              运输公司
            </Animated.Text>
          </View>
        </Pressable>
      </View>
      <View style={{ flex: 1 }} />
      <View>
        <Button
          title={"确认"}
          disabled={selectedIndex == undefined}
          onPress={confirmAction}
          style={styles.button}
        />
      </View>
      <Portal
        visible={tipModalVisible}
        onCancel={closeModal}
        style={styles.modalContainer}
      >
        <View style={styles.modal_view}>
          <TipCard
            status={"primary"}
            title={`身份一旦确认不允许修改！请确认您的身份为：${
              selectedIndex == 0 ? "我要买车" : "运输公司"
            }`}
          />
          <Button
            loading={false}
            style={styles.close_btn}
            title={"我再想想"}
            onPress={closeModal}
          />
          <Button
            loading={false}
            style={styles.confirm_btn}
            title={"确认"}
            status={"control"}
            onPress={confirm}
          />
        </View>
      </Portal>
    </View>
  );
};
const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff",
  },
  tipCard: {
    margin: 16,
  },
  selectContainer: {
    justifyContent: "center",
    alignItems: "center",
    flexDirection: "row",
  },
  separateView: {
    width: 16,
  },
  itemImage: {
    borderRadius: 10,
    width: initialWidth,
    height: initialWidth,
  },
  itemTextView: {
    marginTop: 10,
    justifyContent: "center",
    alignItems: "center",
    height: 25,
  },
  itemText: {
    color: "#333",
    fontWeight: "500",
  },
  button: {
    marginLeft: 16,
    marginRight: 16,
    marginBottom: 40,
  },
  modalContainer: {
    alignItems: "center",
    justifyContent: "center",
  },
  modal_view: {
    backgroundColor: "#fff",
    justifyContent: "center",
    alignItems: "center",
    padding: 16,
    borderRadius: 16,
    width: DeviceInfo.SCREEN_WIDTH - 32,
  },

  confirm_btn: {
    width: DeviceInfo.SCREEN_WIDTH - 64,
    marginTop: 16,
  },

  close_btn: {
    width: DeviceInfo.SCREEN_WIDTH - 64,
    marginTop: 40,
  },
});
export default ChooseTypePage;
