/**
 * 倒计时
 */

import {useEffect, useState} from 'react';

const timer = {};

export const useCountdown = (second = 59, verifyType: string): [number, () => void, () => void] => {
  const [curSecond, setCountdown] = useState(0);

  const stopTime: () => void = () => {
    setCountdown(0);
  };

  const startTime: () => void = () => {
    setCountdown(second);
  };

  useEffect(() => {
    return () => {
      // @ts-ignore
      clearInterval(timer[verifyType]);
    };
  }, []);

  useEffect(() => {
    if (curSecond === second) {
      // @ts-ignore
      timer[verifyType] = setInterval(() => {
        setCountdown(c => c - 1);
      }, 1000);
    } else if (curSecond === 0) {
      // @ts-ignore
      clearInterval(timer[verifyType]);
    }
  }, [curSecond, second]);

  return [curSecond, startTime, stopTime];
};
