import React from 'react';
import {InputProps, TextInput} from './../base/index';
import {Controller, ControllerProps} from 'react-hook-form';

type ControllerInputProps = Omit<ControllerProps, 'render'>;
export type IFormInputProps = InputProps &
  ControllerInputProps & {
    errors?: any;
  };
export const FormInput = (props: IFormInputProps) => {
  const {name, rules, shouldUnregister, defaultValue, control, caption, status, ...inputProps} = props;

  return (
    <Controller
      name={name}
      rules={{
        required: true,
        ...rules,
      }}
      defaultValue={defaultValue}
      shouldUnregister={shouldUnregister}
      control={control}
      render={({field: {onChange, onBlur, value}}) => (
        <TextInput
          onBlur={onBlur}
          onChangeText={value => onChange(value)}
          value={value}
          caption={caption}
          placeholderTextColor={'#E0E0E0'}
          status={status}
          {...inputProps}
        />
      )}
    />
  );
};
