import { Modal, Portal, Text, View } from "components/base";
import DeviceInfo from "constants/DeviceInfo";
import * as React from "react";
import { StyleSheet, TouchableOpacity } from "react-native";
type Props = {
  show: boolean;
  selectItems: Array<string>;
  actionCallback: (index: number) => void;
};
const ActionSheet = (props: Props) => {
  const selectContent = props.selectItems.map((item, index) => {
    const isFirstItem = index == 0;
    const isLastItem = index == props.selectItems.length - 1;
    const buttonStyle = [
      styles.modal_btn,
      isFirstItem ? styles.firstItem : {},
      isLastItem ? styles.lastItem : {},
    ];
    return (
      <TouchableOpacity
        key={"actionsheet-item-" + index}
        activeOpacity={0.8}
        style={buttonStyle}
        onPress={() => {
          props.actionCallback?.(index);
        }}
      >
        <Text style={styles.text_style}>{item}</Text>
      </TouchableOpacity>
    );
  });
  return (
    <Portal visible={props.show}>
      <TouchableOpacity activeOpacity={1} style={styles.container}>
        <View style={styles.modal_view}>
          {selectContent}
          <TouchableOpacity
            activeOpacity={0.8}
            style={styles.close_btn}
            onPress={() => {
              props.actionCallback?.(-1);
            }}
          >
            <Text style={styles.text_style}>取消</Text>
          </TouchableOpacity>
        </View>
      </TouchableOpacity>
    </Portal>
  );
};
const styles = StyleSheet.create({
  container: {
    flex: 1,
    justifyContent: "flex-end",
    alignItems: "center",
    overflow: "hidden",
  },
  modal_btn: {
    width: DeviceInfo.SCREEN_WIDTH - 32,
    height: 55,
    backgroundColor: "#fff",
    alignItems: "center",
    justifyContent: "center",
    borderTopWidth: 0.5,
    borderTopColor: "#f0f0f0",
  },
  firstItem: {
    borderTopLeftRadius: 12,
    borderTopRightRadius: 12,
    borderTopWidth: 0,
  },
  lastItem: {
    borderBottomLeftRadius: 12,
    borderBottomRightRadius: 12,
  },
  modal_view: {
    alignItems: "center",
    justifyContent: "space-between",
    width: DeviceInfo.SCREEN_WIDTH - 32,
    marginBottom: 16,
  },
  close_btn: {
    marginTop: 10,
    width: DeviceInfo.SCREEN_WIDTH - 32,
    height: 55,
    borderRadius: 12,
    backgroundColor: "#fff",
    alignItems: "center",
    justifyContent: "center",
  },
  text_style: {
    color: "#333",
    fontSize: 15,
  },
});
export default ActionSheet;
