import { Button, Text, Portal } from "components/base";
import ImagePickerSingle from "components/imagePicker/ImagePickerSingle";
import TipCard from "components/tip/TipCard";
import DeviceInfo from "constants/DeviceInfo";
import React, { useState } from "react";
import { StyleSheet, View } from "react-native";

const CompanyAuthPage = () => {
  const [imagePath, setImagePath] = useState<string>("");
  const [tipModalVisible, setTipModalVisible] = useState<boolean>(false);

  const submitAction = () => {
    setTipModalVisible(true);
  };
  const closeTipModal = () => {
    setTipModalVisible(false);
  };
  return (
    <View style={styles.container}>
      <TipCard style={styles.tipCard} title={"请上传营业执照照片"} />
      <ImagePickerSingle
        style={styles.imagePicker}
        borderRadius={10}
        onChange={(data) => {
          setImagePath(data.localUri);
        }}
      >
        <View style={styles.pickerContent}>
          <View style={{ flex: 1 }}></View>
          <Text style={styles.pickerText}>{"点击上传\n营业执照照片"}</Text>
        </View>
      </ImagePickerSingle>
      <Button
        title="提交"
        onPress={submitAction}
        disabled={!imagePath}
        style={styles.confirmButton}
      />
      {/* 企业二要素弹层 */}
      <Portal
        visible={tipModalVisible}
        onCancel={closeTipModal}
        style={styles.modalContainer}
      >
        <View style={styles.modal_view}>
          <TipCard status={"warning"} title={"TODO: msg"} />
          <Button
            style={styles.close_btn}
            title={"返回修改"}
            onPress={closeTipModal}
          />
        </View>
      </Portal>
    </View>
  );
};
const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff",
  },
  tipCard: {
    margin: 16,
  },
  imagePicker: {
    width: 200,
    height: 300,
    borderWidth: 1,
    borderColor: "#ccc",
    borderRadius: 10,
    alignSelf: "center",
  },
  pickerContent: {
    flex: 1,
    alignItems: "center",
  },
  pickerText: {
    textAlign: "center",
    color: "#999",
    lineHeight: 20,
    marginBottom: 20,
  },
  confirmButton: {
    margin: 16,
  },
  modalContainer: {
    alignItems: "center",
    justifyContent: "center",
  },
  modal_view: {
    backgroundColor: "#fff",
    justifyContent: "center",
    alignItems: "center",
    padding: 16,
    borderRadius: 16,
    width: DeviceInfo.SCREEN_WIDTH - 32,
  },
  close_btn: {
    width: DeviceInfo.SCREEN_WIDTH - 64,
    marginTop: 40,
  },
});

export default CompanyAuthPage;
