import DeviceInfo from "constants/DeviceInfo";
import React from "react";
import { Text, View } from "react-native";
import Swiper from "react-native-web-swiper";
const bannerHeight = 100;
type SomeSlide = {
  index?: number;
  activeIndex?: number;
};
type BannerProps = {
  timeout?: number;
};
export const SomeSlide = (props: SomeSlide) => {
  const c = 100 * (props.index || 0);
  return (
    <View
      style={{
        flex: 1,
        backgroundColor: `rgba(200, ${c}, 20, 1)`,
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <Text>
        {props.activeIndex == props.index ? `you are #${props.index}` : "other"}
      </Text>
    </View>
  );
};

const HomeTopBanner = (props: BannerProps) => {
  const bannerData = [1, 2, 3, 4, 5, 6];

  return (
    <View style={{ height: bannerHeight, width: DeviceInfo.SCREEN_WIDTH }}>
      <Swiper
        loop
        timeout={props.timeout || 5}
        controlsProps={{
          dotsPos: false,
          prevPos: false,
          nextPos: false,
        }}
      >
        {bannerData.map((data) => {
          return <SomeSlide key={data} />;
        })}
      </Swiper>
    </View>
  );
};

export default HomeTopBanner;
