import DeviceInfo from "constants/DeviceInfo";
import React from "react";
import { Text, View } from "react-native";
import Toast, { ToastProvider } from "react-native-toast-notifications";

const ToastContainer = (props: { children: React.ReactNode }) => {
  return (
    <ToastProvider
      placement="top"
      duration={3000}
      animationType="zoom-in"
      animationDuration={250}
      successColor="green"
      dangerColor="red"
      warningColor="orange"
      normalColor="gray"
      textStyle={{ fontSize: 16 }}
      swipeEnabled={true}
      offset={250} // offset for both top and bottom toasts
      // offsetTop={30}
      // offsetBottom={40}
      // icon={<Icon />}
      // successIcon={<SuccessIcon />}
      // dangerIcon={<DangerIcon />}
      // warningIcon={<WarningIcon />}
      // renderToast={(toastOptions) => JSX.Element} implement custom toast component.
      // renderType={{
      //   custom_type: (toast) => (
      //     <View style={{padding: 15, backgroundColor: 'grey'}}>
      //       <Text>{toast.message}</Text>
      //     </View>
      //   )
      // }}
    >
      {props.children}
    </ToastProvider>
  );
};

export default ToastContainer;

const GlobalToast = () => {
  return (
    <Toast
      placement="top"
      duration={3000}
      animationType="zoom-in"
      animationDuration={250}
      successColor="green"
      dangerColor="red"
      warningColor="orange"
      normalColor="gray"
      offset={DeviceInfo.SCREEN_HEIGHT * 0.5 - 40}
      swipeEnabled={false}
      ref={(ref) => {
        // @ts-ignore
        global["toast"] = ref;
      }}
      renderType={{
        suc: (toast) => (
          <View style={{ padding: 15, backgroundColor: "orange" }}>
            <Text>{toast.message}</Text>
          </View>
        ),
      }}
    />
  );
};

export { GlobalToast };
