import React, {useMemo} from 'react';
import {
  StyleSheet,
  TouchableOpacityProps,
  TouchableOpacity,
  ActivityIndicator,
  GestureResponderEvent,
  StyleProp,
  ViewStyle,
  TextStyle,
} from 'react-native';
import {ViewProps} from './themed';
import {Text} from './Text';
import {LinearGradient} from 'expo-linear-gradient';
import Colors from 'constants/Colors';

const themeStyles = {
  primary: {
    colors: ['#102DFF', Colors.light.tint],
    fontColor: '#fff',
    borderColor: 'transparent',
    loadingColor: '#fff',
  },
  control: {
    colors: ['#fff', '#fff'],
    fontColor: Colors.light.tint,
    borderColor: Colors.light.tint,
    loadingColor: '#888',
  },
  danger: {
    colors: ['#FF565EFF', '#FF565EFF'],
    fontColor: '#fff',
    borderColor: '#FF565EFF',
    loadingColor: '#fff',
  },
  success: {
    colors: ['#21C06DFF', '#21C06DFF'],
    fontColor: '#fff',
    borderColor: '#21C06DFF',
    loadingColor: '#fff',
  },
};

export function Button(
  props: {
    title: string;
    titleStyle?: StyleProp<TextStyle>;
    btnContainerStyle?: ViewStyle;
    loading?: boolean;
    status?: 'primary' | 'control' | 'danger' | 'success';
  } & TouchableOpacityProps &
    ViewProps,
) {
  const {
    title = '',
    titleStyle,
    style,
    loading,
    onPress,
    disabled,
    btnContainerStyle,
    status = 'primary',
    ...touchableOpacityProps
  } = props;
  const {colors, fontColor, borderColor, loadingColor} = themeStyles[status];
  const textStyle = useMemo(() => {
    return StyleSheet.flatten([styles.text, {color: fontColor}, titleStyle]);
  }, [status, titleStyle]);
  // const opacity = useMemo(() => (loading ? 0.6 : 1), [loading]);
  const disabledValue = useMemo(() => {
    return disabled || loading;
  }, [disabled, loading]);

  const onPressAction = (e: GestureResponderEvent) => {
    !loading && onPress && onPress(e);
  };
  // const backgroundColor = useThemeColor({light: lightColor, dark: darkColor}, 'background');
  const containerStyle = useMemo(() => {
    let opacity = disabled || loading ? 0.6 : 1;
    return StyleSheet.flatten([styles.button, {opacity: opacity, borderColor, borderWidth: 1}, btnContainerStyle]);
  }, [disabled, status, loading]);

  return (
    <TouchableOpacity
      style={style}
      activeOpacity={0.8}
      onPress={onPressAction}
      disabled={disabledValue}
      {...touchableOpacityProps}>
      <LinearGradient colors={colors} start={{x: 0, y: 0}} end={{x: 1, y: 1}} style={containerStyle}>
        {loading ? <ActivityIndicator color={loadingColor} size={16} /> : <Text style={textStyle}>{title}</Text>}
      </LinearGradient>
    </TouchableOpacity>
  );
}

const styles = StyleSheet.create({
  button: {
    width: '100%',
    alignItems: 'center',
    justifyContent: 'center',
    paddingVertical: 10,
    // paddingHorizontal: 16,
    borderRadius: 10,
    minHeight: 42,
  },
  text: {
    fontSize: 16,
    fontWeight: 'bold',
    letterSpacing: 0.25,
    color: 'white',
  },
});
