/**
 * @desc Modal, Android iOS Web
 *
 */

import React, {useMemo} from 'react';
import {StyleSheet, ViewStyle, View} from 'react-native';
// @ts-ignore
import Modalbox from 'react-native-modal';

type Props = {
  style?: ViewStyle;
  visible: boolean;
  children: React.ReactNode;
  onCancel?: () => void;
};

export function Modal(props: Props) {
  const {style, visible, children, onCancel} = props;
  const containerStyle = StyleSheet.flatten([styles.container, style]);

  return useMemo(() => {
    return (
      <Modalbox
        isVisible={visible}
        useNativeDriver
        style={styles.modalStyle}
        // swipeToClose={false}
        onModalHide={() => {
          onCancel && onCancel();
        }}
        // backButtonClose={true}
        coverScreen={true}>
        <View style={containerStyle}>{children}</View>
      </Modalbox>
    );
  }, [visible]);
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
  modalStyle: {flex: 1, backgroundColor: 'transparent'},
});
