/**
 * @desc 带有按钮的 Input
 */

import React from 'react';
import {Controller, ControllerProps} from 'react-hook-form';
import {InputVerify, InputVerifyProps} from '../customInputs/InputVerify';

type ControllerInputProps = Omit<ControllerProps, 'render'>;
type Props = InputVerifyProps & ControllerInputProps;
export const FormInputVerify = (props: Props) => {
  const {name, rules, shouldUnregister, defaultValue, control, verifyType, ...inputProps} = props;
  return (
    <Controller
      name={name}
      rules={{
        required: true,
        ...rules,
      }}
      defaultValue={defaultValue}
      shouldUnregister={shouldUnregister}
      control={control}
      render={({field: {onChange, onBlur, value}}) => (
        <InputVerify
          name={name}
          verifyType={verifyType}
          buttonTitle={'发送验证码'}
          placeholder={'输入验证码'}
          placeholderTextColor={'#BFBFC3'}
          onBlur={onBlur}
          onChangeText={value => onChange(value)}
          value={value}
          {...inputProps}
        />
      )}
    />
  );
};
