import React, { useEffect, useState } from "react";
import {
  Text,
  Image,
  StyleSheet,
  TouchableOpacity,
  View,
  ViewStyle,
} from "react-native";
import * as ImagePicker from "expo-image-picker";
import TipCard from "components/tip/TipCard";
import { Button, Portal } from "components/base";
import DeviceInfo from "constants/DeviceInfo";
import { uploadImage } from "services/ImageUploader";

const enum ImageUploadStatus {
  normal = "normal",
  loading = "loading",
  success = "success",
  failed = "failed",
}
type ImageItem = {
  localUri: string;
};
type ComponentProps = {
  style?: ViewStyle;
  children?: React.ReactNode;
  borderRadius: number;
  onChange: (data: ImageItem) => void;
};

const ImagePickerSingle = (props: ComponentProps) => {
  const { onChange } = props;
  const [selectedImage, setSelectedImage] = useState<ImageItem>();
  const [tipModalVisible, setTipModalVisible] = useState<boolean>(false);
  const [uploadStatus, setUploadStatus] = useState<ImageUploadStatus>(
    ImageUploadStatus.success
  );
  const callback = (fileId: string, status: ImageUploadStatus) => {
    setUploadStatus(status);
    if (status === ImageUploadStatus.success) {
      // 上传成功后回调到使用组件的地方
      onChange({ localUri: fileId });
    }
  };
  useEffect(() => {
    if (selectedImage?.localUri) {
      uploadImage(selectedImage?.localUri, `${Date.now()}.jpeg`).then(
        (res) => {
          callback(res?.data?.fileId, ImageUploadStatus.success);
        },
        (err) => {
          callback("", ImageUploadStatus.failed);
        }
      );
    }
  }, [selectedImage]);
  const selectImage = async () => {
    const permissionResult =
      await ImagePicker.requestMediaLibraryPermissionsAsync();
    if (permissionResult.granted === false) {
      setTipModalVisible(true);
      return;
    }
    const pickerResult = await ImagePicker.launchImageLibraryAsync({
      mediaTypes: ImagePicker.MediaTypeOptions.Images,
    });
    if (pickerResult.cancelled === true) {
      return;
    }
    setSelectedImage({ localUri: pickerResult.uri });
    callback("", ImageUploadStatus.loading);
  };
  const removeSelectedImage = () => {
    setSelectedImage(undefined);
    callback("", ImageUploadStatus.normal);
  };

  const closeTipModal = () => {
    setTipModalVisible(false);
  };
  const permissionSetting = () => {};
  return (
    <View
      style={[
        props.style,
        selectedImage?.localUri
          ? {}
          : { borderRadius: props.borderRadius, overflow: "hidden" },
      ]}
    >
      <TouchableOpacity style={styles.container} onPress={selectImage}>
        <View style={[props.style]}>
          {selectedImage?.localUri ? (
            <Image
              style={{
                flex: 1,
                overflow: "hidden",
                borderRadius: props.borderRadius,
              }}
              source={{ uri: selectedImage.localUri }}
            />
          ) : (
            props.children
          )}
          {selectedImage?.localUri ? (
            <TouchableOpacity
              style={styles.deleteButtonContainer}
              onPress={removeSelectedImage}
            >
              <View style={styles.deleteButton}></View>
            </TouchableOpacity>
          ) : null}
        </View>
        <Portal
          visible={tipModalVisible}
          onCancel={closeTipModal}
          style={styles.modalContainer}
        >
          <View style={styles.modal_view}>
            <TipCard status={"warning"} title={"没有权限"} />
            <Button
              style={styles.close_btn}
              title={"去开启权限"}
              onPress={permissionSetting}
            />
          </View>
        </Portal>
      </TouchableOpacity>
      {[ImageUploadStatus.failed, ImageUploadStatus.loading].includes(
        uploadStatus
      ) ? (
        <View style={[styles.coverView, { borderRadius: props.borderRadius }]}>
          <Text style={styles.uploadStatusText}>
            {uploadStatus == ImageUploadStatus.loading
              ? "上传中..."
              : uploadStatus == ImageUploadStatus.failed
              ? "上传失败"
              : ""}
          </Text>
        </View>
      ) : null}
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    borderWidth: 0,
    flex: 1,
  },
  deleteButtonContainer: {
    position: "absolute",
    right: -20,
    top: -20,
    width: 40,
    height: 40,
    // backgroundColor: 'orange',
    justifyContent: "center",
    alignItems: "center",
  },
  deleteButton: {
    width: 26,
    height: 26,
    backgroundColor: "red",
    borderRadius: 13,
  },
  modalContainer: {
    alignItems: "center",
    justifyContent: "center",
  },
  modal_view: {
    backgroundColor: "#fff",
    justifyContent: "center",
    alignItems: "center",
    padding: 16,
    borderRadius: 16,
    width: DeviceInfo.SCREEN_WIDTH - 32,
  },
  close_btn: {
    width: DeviceInfo.SCREEN_WIDTH - 64,
    marginTop: 40,
  },
  coverView: {
    position: "absolute",
    width: "100%",
    height: "100%",
    backgroundColor: "rgba(0,0,0,0.5)",
    justifyContent: "center",
    alignItems: "center",
  },
  uploadStatusText: {
    color: "#ffffff",
  },
});

export default ImagePickerSingle;
